<template>
  <div class="film-box" style="height:100%">
    <div class="content" style="height: 750px" >
      <!--      导航-->
      <div class="nav-box">
        <div class="nav-left" @click="toFront()"><img src="../../assets/image/market_details/back.png" alt="" style="width: 0.44rem;height: 0.42rem;"></div>
        <span class="nav-title">{{$t('common.tixian')}}</span>
        <div class="nav-right" @click="toOrder()">
          <span>{{$t('common.lishijilu')}}</span>
          <img src="../../assets/image/capital/record.png" alt="">
        </div>
      </div>
      <!--      分割线-->
      <div class="dividing-line"></div>
<!--      选择币种-->
      <!-- <div class="select-row">
        <span>BNB</span>
        <div class="right-arrow">
          <van-icon name="arrow" style="vertical-align: middle"/>
        </div>
      </div> -->
      <div class="select-row">
        <span>{{value_Text}}</span>
        <div class="right-arrow">
          <div class="pulldown-box">
          <van-dropdown-menu>
            <van-dropdown-item  @change="usrChange" v-model="value_a" :options="option_a" />
          </van-dropdown-menu>
      </div>
        </div>
      </div>
      <!-- <div class="pulldown-box">
          <van-dropdown-menu>
            <van-dropdown-item  @change="usrChange" v-model="value_a" :options="option_a" />
          </van-dropdown-menu>
      </div> -->
<!--可提数量-->
      <div class="keti-box">
        <p style="margin: 0.45rem 0">{{$t('common.ketishuliang')}}：<span style="color: #FFFFFF">{{total}}</span></p>
        <p style="margin-bottom: 0.25rem">{{value_Text}} {{$t('common.tibidizhi')}}</p>
        <div class="addr-row">
          <input v-if="languageStatus=='en'" type="text" placeholder="withdrawal address"  v-model="withdrawData.address" class="addr-input">
          <input v-else type="text" placeholder="请输入提币地址"  v-model="withdrawData.address" class="addr-input">
          <div class="right-arrow" @click="toAddress()">
            <span>{{$t('common.xuanzedizhi')}}</span>
            <van-icon name="arrow" style="vertical-align: middle"/>
          </div>
        </div>
      </div>
<!--提币数量-->
      <div class="tibi-box">
        <p style="margin-top: .32rem">{{$t('common.tibishuliang')}}</p>
        <div class="tibi-num">
          <input v-if="languageStatus=='en'" type="text" v-model="withdrawData.number" class="ipt" placeholder="enter the quantity">
          <input v-else type="text" v-model="withdrawData.number" class="ipt" placeholder="请输入数量">
          <!-- <span style="color: #FFFFFF">611651</span> -->
          <span style="color: #06A9FF" @click="userWhole()">{{$t('common.total')}}</span>
        </div>
      </div>
      <div class="tibi-box">
        <p style="margin-top: .32rem">{{$t('common.jiaoyimima')}}</p>
        <div class="tibi-num">
          <!-- <span>交易密码</span>  -->
          <input v-if="languageStatus=='en'" type="password" v-model="withdrawData.paypass" class="ipt" placeholder="enter the transaction password">
          <input v-else type="password" v-model="withdrawData.paypass" class="ipt" placeholder="请输入交易密码">
          <!-- <span style="color: #FFFFFF">611651</span> -->
          <!-- <span style="color: #06A9FF" @click="userWhole()">全部</span> -->
        </div>
      </div>
      <!--      手续费  -->
      <div class="shouxu-box">
        <span style="margin-top: .32rem">{{$t('common.shouxufei')}}：</span>
        <span class="shouxu-num">0.3% {{value_Text}}</span>
      </div>
<!--      说明  -->
      <div class="explain">
        <p style="color: #EE4845;margin-bottom: 0.23rem">{{$t('common.shuoming')}}:</p>
        <p style="margin-bottom: 0.1rem">1.{{$t('common.danbizhuanchuzuixiao')}}0.002 {{value_Text}}，{{$t('common.danbrizhuanchuzuida')}}100 {{value_Text}}。</p>
        <p>2.{{$t('common.warning_tips')}}</p>
      </div>
<!--      实际到账-->
      <div class="shiji-box">
        <p>{{$t('common.shijidaozhangshuliang')}}：<span style="color: #EE4845">611621 BNB</span></p>
      </div>
      <div class="bottom-btn" @click="userBut()">{{$t('common.querentibi')}}</div>
      <div style="height: 0.3rem;"></div>
    </div>
  </div>
</template>

<script>
import Vue from 'vue'
import { Icon, DropdownMenu, DropdownItem, Toast } from 'vant'
import { withdraw } from '@/api/user'
import { objKeySort, sgin } from '@/utils/auth'
Vue.use(Icon).use(DropdownMenu).use(DropdownItem).use(Toast)
export default {
  components: {},
  data () {
    return {
      languageStatus: '',
      curHeight: '',
      option_a: [
        { text: 'BTC', value: 1 },
        { text: 'ETH', value: 2 },
        { text: 'USDT', value: 3 },
        { text: 'LBTH', value: 4 }
      ],
      withdrawData: {
        u_id: '',
        number: '',
        symbol: '123456',
        address: '2222',
        paypass: ''
      },
      value_a: 1,
      spanActive: '',
      total: '0', // 总数
      value_Text: '' // 默认币种栏
    }
  },
  created () {
    this.beforeMount()
    this.withdrawData.u_id = sessionStorage.getItem('u_id')
    var objType = sessionStorage.getItem('typess')
    var userAdd = sessionStorage.getItem('userAdderss')
    this.getLanguageStatus()
    if (userAdd === null) {
      this.withdrawData.address = ''
    } else {
      this.withdrawData.address = userAdd
    }
    var types = JSON.parse(objType)
    // if (this.spanActive === 1) {
    this.option_a = []
    for (var i = 0; i < 4; i++) {
      var obj = {}
      if (i === 0) {
        obj.text = 'BTC'
        obj.total = types.btc_available_balance
        obj.value = i + 1
        this.option_a.push(obj)
      } else if (i === 1) {
        obj.text = 'ETH'
        obj.total = types.eth_available_balance
        obj.value = i + 1
        this.option_a.push(obj)
      } else if (i === 2) {
        obj.text = 'USDT'
        obj.total = types.usdt_available_balance
        obj.value = i + 1
        this.option_a.push(obj)
      } else if (i === 3) {
        obj.text = 'LBTH'
        obj.total = types.lbth_available_balance
        obj.value = i + 1
        this.option_a.push(obj)
      }
    }
    // }
    this.total = this.option_a[0].total
    this.withdrawData.symbol = this.option_a[0].text.toLowerCase()
    this.value_Text = this.option_a[0].text
  },
  methods: {
    getLanguageStatus () {
      console.log(this.$store.state.language)
      this.languageStatus = this.$store.state.language
    },
    userBut () {
      var that = this
      if (that.withdrawData.number === '') {
        that.$toast({ message: '提现数量不能为空', duration: 1500 })
        return
      }
      if (that.withdrawData.address === '') {
        that.$toast({ message: '请选择提现地址', duration: 1500 })
        return
      }
      if (that.withdrawData.paypass === '') {
        that.$toast({ message: '请选择交易密码', duration: 1500 })
        return
      }
      this.withdrawData.symbol = this.value_Text.toLowerCase()
      this.withdrawFun()
    },
    userWhole () {
      this.withdrawData.number = this.total
    },
    withdrawFun () {
      var that = this
      var data = objKeySort(this.withdrawData)
      var token = sgin(data)
      this.withdrawData.token = token
      withdraw(this.withdrawData).then(res => {
        if (res.code === 0) {
          that.$toast({ message: '提现成功', duration: 1500 })
          // that.withdrawData.num = ''
          // number: '',
          // symbol: '123456',
          // address: '',
          // paypass: ''
          delete this.withdrawData.token
        }
      }).catch((err) => {
        that.$toast({ message: err, duration: 1500 })
        // that.withdrawData.num = ''
        delete this.withdrawData.token
      })
    },
    usrChange (value) {
      var that = this
      for (const [i, v] of that.option_a.entries()) {
        if (v.value === value) {
          console.log(i)
          that.total = v.total
          that.value_Text = v.text
        }
      }
    },
    beforeMount (height) {
      var h = document.documentElement.clientHeight || document.body.clientHeight
      this.curHeight = h
    },
    toFront () {
      history.go(-1)
    },
    toOrder () {
      this.$router.push({
        path: '/personal/order',
        query: {
          types: '提现'
        }
      })
    },
    toAddress () {
      this.$router.push({
        path: '/personal/addressList',
        query: {
          adderss: 1
        }
      })
    }
  }
}
</script>
