    color:rgba(218,218,221,0.3);
  }
  .keti-box{
    padding: 0 0.3rem;
    padding-bottom: 0.4rem;
    border-bottom: 0.02rem solid #12172B;
    text-align: left;
  }
  .tibi-num{
    display: flex;
