  }
  .pulldown-box >>> .van-dropdown-menu__item{
    background: #1B213B;
    color: rgba(255, 255, 255, 0.7);
    /* 注释了 */
    padding: 0 0.5rem;
  }
  .pulldown-box >>> .van-ellipsis{
    /*text-align: left;*/
    /*background-color: orange;*/
    margin-left: -1rem;
